import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,

  name: PropTypes.string,
  xcoord: PropTypes.number,
  ycoord: PropTypes.number,
});

export const formValues = PropTypes.shape({
  name: PropTypes.string,
  xcoord: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  ycoord: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
});

export const formTouched = PropTypes.shape({
  name: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  xcoord: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  ycoord: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  name: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  xcoord: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  ycoord: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
